.media-card {
  cursor: pointer;
  transition: transform 0.2s;
}

.media-card:hover {
  transform: scale(1.03);
}

.card-poster {
  position: relative;
  aspect-ratio: 2/3;
  border-radius: 8px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.1);
}

.card-poster img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.no-poster {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: rgba(255, 255, 255, 0.3);
}

.card-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.7), transparent);
}

.media-type {
  background: rgba(0, 0, 0, 0.6);
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: 600;
  color: white;
  text-transform: uppercase;
}

.status-badge {
  padding: 0.2rem 0.5rem;
  border-radius: 4px;
  font-size: 0.65rem;
  font-weight: 600;
}

.status-badge.available {
  background: rgba(46, 213, 115, 0.9);
  color: white;
}

.status-badge.downloaded {
  background: rgba(46, 213, 115, 0.9);
  color: white;
}

.status-badge.queued {
  background: rgba(255, 193, 7, 0.9);
  color: #000;
}

.status-badge.missing {
  background: rgba(255, 107, 107, 0.9);
  color: white;
}

.status-badge.requested {
  background: rgba(52, 152, 219, 0.9);
  color: white;
}

.card-hover {
  position: absolute;
  inset: 0;
  background: rgba(229, 9, 20, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.2s;
}

.media-card:hover .card-hover {
  opacity: 1;
}

.view-details {
  color: white;
  font-weight: 600;
  font-size: 0.9rem;
}

.card-info {
  padding: 0.75rem 0.25rem;
}

.card-title {
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
  margin: 0 0 0.25rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.card-meta {
  display: flex;
  gap: 0.5rem;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.6);
}

.card-rating {
  color: #ffd700;
}
