.setup-wizard {
  min-height: 100vh;
  background: linear-gradient(135deg, #0f0f23 0%, #1a1a3e 50%, #0f0f23 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}

.setup-container {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  padding: 2.5rem;
  width: 100%;
  max-width: 700px;
  backdrop-filter: blur(10px);
}

.setup-header {
  text-align: center;
  margin-bottom: 2rem;
}

.setup-header h1 {
  color: white;
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.setup-header p {
  color: rgba(255, 255, 255, 0.6);
}

/* Progress Steps */
.setup-progress {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  position: relative;
}

.setup-progress::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 40px;
  right: 40px;
  height: 2px;
  background: rgba(255, 255, 255, 0.2);
}

.progress-step {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 1;
}

.step-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  border: 2px solid rgba(255, 255, 255, 0.2);
  display: flex;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.5);
  font-weight: 600;
  margin-bottom: 0.5rem;
  transition: all 0.3s;
}

.progress-step.active .step-number {
  background: #e50914;
  border-color: #e50914;
  color: white;
}

.progress-step.current .step-number {
  box-shadow: 0 0 0 4px rgba(229, 9, 20, 0.3);
}

.step-label {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
  text-align: center;
}

.progress-step.active .step-label {
  color: white;
}

/* Alerts */
.alert {
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.alert-error {
  background: rgba(229, 9, 20, 0.2);
  border: 1px solid rgba(229, 9, 20, 0.5);
  color: #ff6b6b;
}

.alert-success {
  background: rgba(46, 213, 115, 0.2);
  border: 1px solid rgba(46, 213, 115, 0.5);
  color: #2ed573;
}

/* Form */
.setup-form h2 {
  color: white;
  margin-bottom: 0.5rem;
}

.setup-form > p {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 1.5rem;
}

.setup-form > p a {
  color: #e50914;
}

.form-section {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

.form-section h3 {
  color: white;
  font-size: 1rem;
  margin-bottom: 1rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  color: rgba(255, 255, 255, 0.8);
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-size: 1rem;
  transition: all 0.2s;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #e50914;
  background: rgba(255, 255, 255, 0.15);
}

.form-group input::placeholder {
  color: rgba(255, 255, 255, 0.4);
}

.form-group small {
  display: block;
  margin-top: 0.25rem;
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.8rem;
}

.form-group small a {
  color: #e50914;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

@media (max-width: 600px) {
  .form-row {
    grid-template-columns: 1fr;
  }
}

/* Test Results */
.test-result {
  padding: 0.75rem 1rem;
  border-radius: 6px;
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

.test-result.success {
  background: rgba(46, 213, 115, 0.2);
  color: #2ed573;
}

.test-result.error {
  background: rgba(229, 9, 20, 0.2);
  color: #ff6b6b;
}

/* Buttons */
.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #e50914, #b20710);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(229, 9, 20, 0.4);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.2);
}

.btn-small {
  padding: 0.5rem 1rem;
  font-size: 0.85rem;
}

.btn-large {
  padding: 1rem 2rem;
  font-size: 1.1rem;
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 1.5rem;
}

/* Complete */
.setup-complete {
  text-align: center;
  padding: 2rem 0;
}

.complete-icon {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.setup-complete h2 {
  color: white;
  margin-bottom: 0.5rem;
}

.setup-complete > p {
  color: rgba(255, 255, 255, 0.6);
  margin-bottom: 2rem;
}

.setup-summary {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  text-align: left;
}

.setup-summary h3 {
  color: white;
  font-size: 1rem;
  margin-bottom: 1rem;
}

.setup-summary ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.setup-summary li {
  color: #2ed573;
  padding: 0.5rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.setup-summary li:last-child {
  border-bottom: none;
}

/* Restore Option Styles */
.restore-option-banner {
  background: rgba(229, 9, 20, 0.1);
  border: 1px solid rgba(229, 9, 20, 0.3);
  border-radius: 8px;
  padding: 0.75rem 1rem;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1rem;
}

.restore-option-banner span {
  color: rgba(255, 255, 255, 0.8);
  font-size: 0.9rem;
}

.restore-option-banner .btn-link {
  background: none;
  border: none;
  color: #e50914;
  cursor: pointer;
  font-size: 0.9rem;
  padding: 0;
  text-decoration: underline;
}

.restore-option-banner .btn-link:hover {
  color: #ff3344;
}

.restore-upload-area {
  text-align: center;
  padding: 2rem;
  border: 2px dashed rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  margin-bottom: 1.5rem;
}

.restore-upload-area .btn-large {
  padding: 1rem 2rem;
  font-size: 1.1rem;
}

.backup-preview {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  padding: 1.5rem;
  margin-top: 1.5rem;
}

.backup-preview h3 {
  color: white;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.backup-info {
  margin-bottom: 1rem;
}

.backup-info p {
  color: rgba(255, 255, 255, 0.7);
  margin: 0.25rem 0;
  font-size: 0.9rem;
}

.backup-summary {
  list-style: none;
  padding: 0;
  margin: 0 0 1.5rem 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 0.5rem;
}

.backup-summary li {
  background: rgba(255, 255, 255, 0.05);
  padding: 0.5rem 0.75rem;
  border-radius: 6px;
  color: rgba(255, 255, 255, 0.9);
  font-size: 0.9rem;
}

.backup-warning {
  background: rgba(255, 193, 7, 0.15);
  border: 1px solid rgba(255, 193, 7, 0.3);
  border-radius: 8px;
  padding: 0.75rem 1rem;
  margin-bottom: 1.5rem;
  color: #ffc107;
  font-size: 0.85rem;
}

.backup-warning strong {
  color: #ffc107;
}
