.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  z-index: 1000;
  overflow-y: auto;
}

.modal-content {
  position: relative;
  background: #1a1a3e;
  border-radius: 16px;
  width: 100%;
  max-width: 900px;
  max-height: 90vh;
  overflow: hidden;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.5);
}

.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  width: 40px;
  height: 40px;
  background: rgba(0, 0, 0, 0.6);
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  z-index: 10;
  transition: all 0.2s;
}

.modal-close:hover {
  background: rgba(229, 9, 20, 0.8);
}

.modal-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 400px;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top-color: #e50914;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 300px;
  background-size: cover;
  background-position: center top;
  opacity: 0.3;
  mask-image: linear-gradient(to bottom, black, transparent);
  -webkit-mask-image: linear-gradient(to bottom, black, transparent);
}

.modal-body {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 2rem;
  padding: 2rem;
  position: relative;
  overflow-y: auto;
  max-height: 90vh;
}

@media (max-width: 700px) {
  .modal-body {
    grid-template-columns: 1fr;
  }
  
  .modal-poster {
    max-width: 200px;
    margin: 0 auto;
  }
}

.modal-poster {
  aspect-ratio: 2/3;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
}

.modal-poster img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.modal-poster .no-poster {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  font-size: 4rem;
  color: rgba(255, 255, 255, 0.3);
}

.modal-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.modal-header h2 {
  color: white;
  font-size: 1.75rem;
  margin: 0 0 0.5rem;
}

.modal-meta {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.modal-meta span {
  background: rgba(255, 255, 255, 0.1);
  padding: 0.25rem 0.75rem;
  border-radius: 4px;
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.8);
}

.meta-rating {
  color: #ffd700 !important;
}

.status-banner {
  padding: 0.75rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  text-align: center;
}

.status-banner.available,
.status-banner.downloaded {
  background: rgba(46, 213, 115, 0.2);
  color: #2ed573;
}

.status-banner.queued {
  background: rgba(255, 193, 7, 0.2);
  color: #ffc107;
}

.status-banner.missing {
  background: rgba(255, 107, 107, 0.2);
  color: #ff6b6b;
}

.status-banner.requested {
  background: rgba(52, 152, 219, 0.2);
  color: #3498db;
}

.status-banner.not-available {
  background: rgba(255, 255, 255, 0.1);
  color: rgba(255, 255, 255, 0.6);
}

.modal-overview {
  color: rgba(255, 255, 255, 0.8);
  line-height: 1.6;
  margin: 0;
}

/* Season Selection */
.season-selection {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 1rem;
}

.season-selection h3 {
  color: white;
  font-size: 1rem;
  margin: 0 0 0.75rem;
}

.season-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.season-option {
  display: flex;
  flex-direction: column;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.2s;
}

.season-option:hover {
  background: rgba(255, 255, 255, 0.1);
}

.season-option.selected {
  background: rgba(229, 9, 20, 0.2);
  border-color: #e50914;
}

.season-option input {
  display: none;
}

.season-name {
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
}

.season-episodes {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.75rem;
}

.season-actions {
  display: flex;
  gap: 1rem;
}

.btn-link {
  background: none;
  border: none;
  color: #e50914;
  font-size: 0.85rem;
  cursor: pointer;
  padding: 0;
}

.btn-link:hover {
  text-decoration: underline;
}

/* Action Buttons */
.modal-actions {
  display: flex;
  gap: 1rem;
  margin-top: auto;
  padding-top: 1rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #e50914, #b20710);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(229, 9, 20, 0.4);
}

.btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.btn-secondary:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.2);
}

.btn-success {
  background: rgba(46, 213, 115, 0.2);
  color: #2ed573;
}
