.settings-page {
  max-width: 900px;
  margin: 0 auto;
}

.page-header {
  margin-bottom: 2rem;
}

.page-header h1 {
  color: white;
  font-size: 2rem;
  margin: 0 0 0.5rem;
}

.page-header p {
  color: rgba(255, 255, 255, 0.6);
  margin: 0;
}

.settings-layout {
  display: grid;
  grid-template-columns: 200px 1fr;
  gap: 2rem;
}

.settings-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.nav-btn {
  padding: 0.75rem 1rem;
  background: transparent;
  border: none;
  border-radius: 8px;
  color: rgba(255, 255, 255, 0.6);
  font-size: 0.95rem;
  text-align: left;
  cursor: pointer;
  transition: all 0.2s;
}

.nav-btn:hover {
  color: white;
  background: rgba(255, 255, 255, 0.05);
}

.nav-btn.active {
  color: white;
  background: rgba(229, 9, 20, 0.2);
  border-left: 3px solid #e50914;
}

.settings-content {
  min-width: 0;
}

.message {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
}

.message.success {
  background: rgba(46, 213, 115, 0.2);
  border: 1px solid rgba(46, 213, 115, 0.5);
  color: #2ed573;
}

.message.error {
  background: rgba(229, 9, 20, 0.2);
  border: 1px solid rgba(229, 9, 20, 0.5);
  color: #ff6b6b;
}

.message button {
  background: transparent;
  border: none;
  color: inherit;
  cursor: pointer;
  font-size: 1.2rem;
  opacity: 0.7;
}

.message button:hover {
  opacity: 1;
}

.settings-section {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 1.5rem;
}

.settings-section h2 {
  color: white;
  margin: 0 0 1.5rem;
  font-size: 1.2rem;
}

.info-card {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1.5rem;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
}

.info-row:not(:last-child) {
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.info-label {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
}

.info-value {
  color: white;
}

.role-badge {
  padding: 0.2rem 0.6rem;
  border-radius: 4px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
}

.role-badge.admin {
  background: rgba(229, 9, 20, 0.2);
  color: #e50914;
}

.role-badge.user {
  background: rgba(52, 152, 219, 0.2);
  color: #3498db;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  color: rgba(255, 255, 255, 0.7);
  font-size: 0.85rem;
  margin-bottom: 0.5rem;
}

.form-group input {
  width: 100%;
  padding: 0.75rem;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 6px;
  color: white;
  font-size: 0.95rem;
}

.form-group input:focus {
  outline: none;
  border-color: #e50914;
}

.form-group input::placeholder {
  color: rgba(255, 255, 255, 0.3);
}

.form-hint {
  display: block;
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.4);
  margin-top: 0.5rem;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.btn-primary {
  background: #e50914;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background: #b8070f;
}

.btn-danger {
  background: rgba(255, 107, 107, 0.2);
  color: #ff6b6b;
  border: 1px solid rgba(255, 107, 107, 0.3);
}

.btn-danger:hover:not(:disabled) {
  background: rgba(255, 107, 107, 0.3);
}

.danger-zone {
  margin-top: 2rem;
  padding-top: 2rem;
  border-top: 1px solid rgba(255, 107, 107, 0.3);
}

.danger-zone h3 {
  color: #ff6b6b;
  font-size: 1rem;
  margin: 0 0 0.5rem;
}

.danger-zone p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 0.9rem;
  margin: 0 0 1rem;
}

/* Server List */
.server-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.server-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 8px;
}

.server-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.server-name {
  color: white;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.primary-badge {
  font-size: 0.7rem;
  padding: 0.15rem 0.4rem;
  background: rgba(46, 213, 115, 0.2);
  color: #2ed573;
  border-radius: 4px;
  font-weight: 600;
  text-transform: uppercase;
}

.server-url {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.5);
}

.server-features {
  display: flex;
  gap: 0.5rem;
}

.feature {
  font-size: 0.8rem;
  padding: 0.25rem 0.5rem;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  color: rgba(255, 255, 255, 0.7);
}

.empty-state {
  text-align: center;
  padding: 3rem 2rem;
  color: rgba(255, 255, 255, 0.6);
}

.empty-icon {
  font-size: 3rem;
  display: block;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.empty-hint {
  font-size: 0.85rem;
  margin-top: 0.5rem;
  opacity: 0.6;
}

@media (max-width: 768px) {
  .settings-layout {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .settings-nav {
    flex-direction: row;
    overflow-x: auto;
    padding-bottom: 0.5rem;
  }

  .nav-btn {
    white-space: nowrap;
  }

  .nav-btn.active {
    border-left: none;
    border-bottom: 3px solid #e50914;
  }

  .server-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
}
