/* BigFlix Netflix-style Intro Animation */

:root {
  --primary-colour: #E50914;
  --shadow-colour: black;
  --shadow-intensity: 0.6;
}

/* Base fill for all letter paths */
.base {
  fill: var(--primary-colour);
  transition: fill 0.4s;
}

/* Shadow gradient stops */
.shadow-start {
  stop-color: var(--shadow-colour);
  stop-opacity: var(--shadow-intensity);
}

.shadow-end {
  stop-color: var(--shadow-colour);
  stop-opacity: 0;
}

/* Main container */
.bigflix-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: black;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999;
  overflow: hidden;
}

/* SVG styling - hidden initially until GSAP loads */
.bigflix-svg {
  width: auto;
  height: auto;
  max-width: 80vw;
  max-height: 30vh;
  visibility: hidden; /* Hidden until GSAP is ready */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .bigflix-svg {
    max-width: 90vw;
    max-height: 20vh;
  }
}

@media (max-width: 480px) {
  .bigflix-svg {
    max-width: 95vw;
    max-height: 15vh;
  }
}

/* Prefers reduced motion */
@media (prefers-reduced-motion: reduce) {
  .bigflix-container {
    transition: opacity 0.3s ease-out;
  }
  
  .bigflix-svg {
    visibility: visible !important;
    opacity: 1 !important;
    transform: none !important;
  }
}
